.NavPageButtons {
    width: 200px;
    padding: 20px 0;
    text-align: center;
    cursor: pointer;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s
}

.NavPageButtons:hover {
    font-size: 1.2em;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}


