img.BioPic {
    position: absolute;
    height: 70%;
    width: auto;
    left: 150px;
    top: 150px;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    -webkit-filter: blur(25px);
            filter: blur(25px);
    z-index: 0;
    /*box-shadow: 2px 0px 5px darkgray;*/
}

.BioPic:hover {
    border-radius: 50%;
    -webkit-filter: none;
            filter: none;
}

.BioPicAfter {
    position: absolute;
    height: 70%;
    width: auto;
    left: 150px;
    top: 175px;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    border-radius: 50%;
    z-index: 0;
}

.BioPicAfter:hover {
    -webkit-transform-origin: center;
        -ms-transform-origin: center;
            transform-origin: center;
    -webkit-transform: scale(1.1, 1.1);
        -ms-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}

.BioPicScrolling {
    z-index: 0;
}


