.NavPage {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: fixed;
    background: white;
    border: white 50px solid;
    width: 100vw;
    height: 100vh;
    top: -100%;
    opacity: 0;
    z-index: 1;
    -webkit-transition: all 400ms;
    -o-transition: all 400ms;
    transition: all 400ms;
    pointer-events: none;
}

.NavPageOpen {
    position: fixed;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: white 300px solid;
    background: -webkit-gradient(linear, left top, left bottom, from(blue), color-stop(darkblue), color-stop(indigo), color-stop(purple), to(darkred));
    background: -webkit-linear-gradient(blue, darkblue, indigo, purple, darkred);
    background: -o-linear-gradient(blue, darkblue, indigo, purple, darkred);
    background: linear-gradient(blue, darkblue, indigo, purple, darkred);
    width: 100vw;
    height: 100vh;
    -webkit-transition: all 400ms ;
    -o-transition: all 400ms ;
    transition: all 400ms ;
    pointer-events: all;
    z-index: 1;
    opacity: 1;
} 

.NavPageAfter {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: fixed;
    background: white;
    border: white 50px solid;
    width: 99vw;
    height: 100vh;
    z-index: 1;
    -webkit-transition: all 400ms;
    -o-transition: all 400ms;
    transition: all 400ms;
    pointer-events: none;
    opacity: 0;
}




